      <span class="hljs-tag">&lt;<span class="hljs-name">mu-select</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"Error"</span> <span class="hljs-attr">error-text</span>=<span class="hljs-string">"Please select an option"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"normal.value2"</span> <span class="hljs-attr">full-width</span>&gt;</span>
