      <span class="hljs-tag">&lt;/<span class="hljs-name">mu-select</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">mu-col</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-col</span> <span class="hljs-attr">span</span>=<span class="hljs-string">"12"</span> <span class="hljs-attr">lg</span>=<span class="hljs-string">"4"</span> <span class="hljs-attr">sm</span>=<span class="hljs-string">"6"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">mu-select</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"Disabled"</span> <span class="hljs-attr">disabled</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"normal.value4"</span> <span class="hljs-attr">full-width</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">mu-option</span> <span class="hljs-attr">v-for</span>=<span class="hljs-string">"option in options"</span> <span class="hljs-attr">:key</span>=<span class="hljs-string">"option"</span> <span class="hljs-attr">:label</span>=<span class="hljs-string">"option"</span> <span class="hljs-attr">:value</span>=<span class="hljs-string">"option"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-option</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">mu-select</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">mu-col</span>&gt;</span>
