<h2 id="duo-xuan"><a class="header-anchor" href="#duo-xuan" aria-hidden="true">¶</a> 多选</h2>
<p>设置 <code v-pre="">multiple</code> 将选择框变为多选， <code v-pre="">separator</code> 更改分隔符, <code v-pre="">chips</code> 使用 <code v-pre="">mu-chip</code> 组件展示已选择项</p>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;mu-container&gt;\n  &lt;mu-row gutter&gt;\n    &lt;mu-col span=\&quot;12\&quot; lg=\&quot;4\&quot; sm=\&quot;6\&quot;&gt;\n      &lt;mu-select label=\&quot;Multi Select\&quot; multiple v-model=\&quot;multi.value1\&quot; full-width&gt;\n        &lt;mu-option v-for=\&quot;city,index in citys\&quot; :key=\&quot;city\&quot; :label=\&quot;city\&quot; :value=\&quot;city\&quot;&gt;&lt;/mu-option&gt;\n      &lt;/mu-select&gt;\n    &lt;/mu-col&gt;\n    &lt;mu-col span=\&quot;12\&quot; lg=\&quot;4\&quot; sm=\&quot;6\&quot;&gt;\n      &lt;mu-select label=\&quot;Separator\&quot; multiple separator=\&quot;/\&quot; v-model=\&quot;multi.value2\&quot; full-width&gt;\n        &lt;mu-option v-for=\&quot;city,index in citys\&quot; :key=\&quot;city\&quot; :label=\&quot;city\&quot; :value=\&quot;city\&quot;&gt;&lt;/mu-option&gt;\n      &lt;/mu-select&gt;\n    &lt;/mu-col&gt;\n    &lt;mu-col span=\&quot;12\&quot; lg=\&quot;4\&quot; sm=\&quot;6\&quot;&gt;\n      &lt;mu-select label=\&quot;Use Chips\&quot; multiple chips=\&quot;\&quot; v-model=\&quot;multi.value3\&quot; full-width&gt;\n        &lt;mu-option v-for=\&quot;city,index in citys\&quot; :key=\&quot;city\&quot; :label=\&quot;city\&quot; :value=\&quot;city\&quot;&gt;&lt;/mu-option&gt;\n      &lt;/mu-select&gt;\n    &lt;/mu-col&gt;\n  &lt;/mu-row&gt;\n&lt;/mu-container&gt;\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n  data () {\n    return {\n      citys: [\n        'Alabama', 'Alaska', 'American Samoa', 'Arizona',\n        'Arkansas', 'California', 'Colorado', 'Connecticut',\n        'Delaware', 'District of Columbia', 'Federated States of Micronesia',\n        'Florida', 'Georgia', 'Guam', 'Hawaii', 'Idaho',\n        'Illinois', 'Indiana', 'Iowa', 'Kansas', 'Kentucky',\n        'Louisiana', 'Maine', 'Marshall Islands', 'Maryland',\n        'Massachusetts', 'Michigan', 'Minnesota', 'Mississippi',\n        'Missouri', 'Montana', 'Nebraska', 'Nevada',\n        'New Hampshire', 'New Jersey', 'New Mexico', 'New York',\n        'North Carolina', 'North Dakota', 'Northern Mariana Islands', 'Ohio',\n        'Oklahoma', 'Oregon', 'Palau', 'Pennsylvania', 'Puerto Rico',\n        'Rhode Island', 'South Carolina', 'South Dakota', 'Tennessee',\n        'Texas', 'Utah', 'Vermont', 'Virgin Island', 'Virginia',\n        'Washington', 'West Virginia', 'Wisconsin', 'Wyoming'\n      ],\n      multi: {\n        value1: [],\n        value2: [],\n        value3: []\n      }\n    }\n  }\n}\n&quot;,&quot;style&quot;:null}">
                    <div class="source" slot="source"><mu-container>
  <mu-row gutter="">
    <mu-col span="12" lg="4" sm="6">
      <mu-select label="Multi Select" multiple v-model="multi.value1" full-width="">
        <mu-option v-for="city,index in citys" :key="city" :label="city" :value="city"></mu-option>
      </mu-select>
    </mu-col>
    <mu-col span="12" lg="4" sm="6">
      <mu-select label="Separator" multiple separator="/" v-model="multi.value2" full-width="">
        <mu-option v-for="city,index in citys" :key="city" :label="city" :value="city"></mu-option>
      </mu-select>
    </mu-col>
    <mu-col span="12" lg="4" sm="6">
      <mu-select label="Use Chips" multiple chips="" v-model="multi.value3" full-width="">
        <mu-option v-for="city,index in citys" :key="city" :label="city" :value="city"></mu-option>
      </mu-select>
    </mu-col>
  </mu-row>
</mu-container>

</div>
                    
