        <span class="hljs-tag">&lt;<span class="hljs-name">mu-option</span> <span class="hljs-attr">v-for</span>=<span class="hljs-string">"city,index in citys"</span> <span class="hljs-attr">:key</span>=<span class="hljs-string">"city"</span> <span class="hljs-attr">:label</span>=<span class="hljs-string">"city"</span> <span class="hljs-attr">:value</span>=<span class="hljs-string">"city"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-option</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">mu-select</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">mu-col</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">mu-row</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">mu-container</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data () {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">citys</span>: [
        <span class="hljs-string">'Alabama'</span>, <span class="hljs-string">'Alaska'</span>, <span class="hljs-string">'American Samoa'</span>, <span class="hljs-string">'Arizona'</span>,
        <span class="hljs-string">'Arkansas'</span>, <span class="hljs-string">'California'</span>, <span class="hljs-string">'Colorado'</span>, <span class="hljs-string">'Connecticut'</span>,
        <span class="hljs-string">'Delaware'</span>, <span class="hljs-string">'District of Columbia'</span>, <span class="hljs-string">'Federated States of Micronesia'</span>,
        <span class="hljs-string">'Florida'</span>, <span class="hljs-string">'Georgia'</span>, <span class="hljs-string">'Guam'</span>, <span class="hljs-string">'Hawaii'</span>, <span class="hljs-string">'Idaho'</span>,
        <span class="hljs-string">'Illinois'</span>, <span class="hljs-string">'Indiana'</span>, <span class="hljs-string">'Iowa'</span>, <span class="hljs-string">'Kansas'</span>, <span class="hljs-string">'Kentucky'</span>,
