       'Illinois', 'Indiana', 'Iowa', 'Kansas', 'Kentucky',\n        'Louisiana', 'Maine', 'Marshall Islands', 'Maryland',\n        'Massachusetts', 'Michigan', 'Minnesota', 'Mississippi',\n        'Missouri', 'Montana', 'Nebraska', 'Nevada',\n        'New Hampshire', 'New Jersey', 'New Mexico', 'New York',\n        'North Carolina', 'North Dakota', 'Northern Mariana Islands', 'Ohio',\n        'Oklahoma', 'Oregon', 'Palau', 'Pennsylvania', 'Puerto Rico',\n        'Rhode Island', 'South Carolina', 'South Dakota', 'Tennessee',\n        'Texas', 'Utah', 'Vermont', 'Virgin Island', 'Virginia',\n        'Washington', 'West Virginia', 'Wisconsin', 'Wyoming'\n      ],\n      custom: {\n        value1: '',\n        value2: []\n      }\n    }\n  }\n}\n&quot;,&quot;style&quot;:null}">
                    <div class="source" slot="source"><mu-container>
  <mu-row gutter="">
    <mu-col span="12" sm="6">
      <mu-select label="Option Slot" v-model="custom.value1" full-width="">
        <mu-option v-for="language,index in languages" avatar="" :key="language" :label="language" :value="language">
          <mu-list-item-action avatar="">
            <mu-avatar :size="36" color="primary">
              {{language.substring(0, 1)}}
            </mu-avatar>
          </mu-list-item-action>
          <mu-list-item-content>
            <mu-list-item-title>{{language}}</mu-list-item-title>
          </mu-list-item-content>
        </mu-option>
      </mu-select>
    </mu-col>
    <mu-col span="12" sm="6">
      <mu-select label="Selection Scoped Slot" multiple chips="" v-model="custom.value2" full-width="">
        <template slot="selection" slot-scope="scope">
          <mu-chip :selected="scope.selected" color="teal">
            {{scope.label}}
          </mu-chip>
        </template>
        <mu-option v-for="language,index in languages" :key="language" :label="language" :value="language"></mu-option>
      </mu-select>
    </mu-col>
  </mu-row>
</mu-container>

</div>
                    
