<p><mu-alert color="info">mu-select 支持 select 标签所有的原生属性和事件。</mu-alert></p>
<h2 id="select-props"><a class="header-anchor" href="#select-props" aria-hidden="true">¶</a> Select Props</h2>
<table>
<thead>
<tr>
<th>参数</th>
<th>介绍</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>multiple</td>
<td>是否多选</td>
<td>Boolean</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>filterable</td>
<td>是否可搜索</td>
<td>Boolean</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>chips</td>
<td>已选项是否用 <code v-pre="">mu-chip</code> 组件显示</td>
<td>Boolean</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>max-height</td>
<td>弹出选择列表的最大高度</td>
<td>Number/String</td>
<td>—</td>
<td>300</td>
</tr>
<tr>
<td>separator</td>
<td>多选时的文本分隔符</td>
<td>String</td>
<td>—</td>
<td>,</td>
</tr>
<tr>
<td>popover-class</td>
<td>弹出框的样式</td>
<td>String</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>no-data-text</td>
<td>未搜索数据时显示的文本</td>
<td>String</td>
<td>—</td>
<td>暂无数据显示</td>
</tr>
<tr>
<td>textline</td>
<td>弹出列表项显示行数，设置之后会改变选项的高度</td>
<td>String</td>
<td>two-line/three-line</td>
<td>—</td>
</tr>
<tr>
<td>dense</td>
<td>减小弹出列表最大高度</td>
<td>Boolean</td>
<td>—</td>
<td>true</td>
</tr>
<tr>
<td>value</td>
<td>用于 <code v-pre="">v-model</code> 绑定</td>
<td>—</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>color</td>
<td>输入框颜色</td>
<td>String</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>icon</td>
<td>输入框左边的图标</td>
<td>String</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>label</td>
<td>标签文本</td>
<td>String</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>label-float</td>
<td>标签是否浮动</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>underline-show</td>
<td>是否显示底部的横线</td>
<td>Boolean</td>
<td>—</td>
<td>true</td>
</tr>
<tr>
<td>error-text</td>
<td>错误提醒文字，如果此参数有值，那么输入框会转为错误的状态, 显示效果将会更改</td>
<td>String</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>help-text</td>
<td>帮助文字</td>
<td>String</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>full-width</td>
<td>是否将宽度设置为 100%, 默认 256px</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>disabled</td>
<td>输入框是否不可用</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
</tbody>
</table>
<h2 id="select-scope-slots"><a class="header-anchor" href="#select-scope-slots" aria-hidden="true">¶</a> Select Scope Slots</h2>
<table>
<thead>
<tr>
<th>名称</th>
<th>介绍</th>
<th>参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>selection</td>
<td>已选项的插槽</td>
<td>{ selected, label, value, index, disabled}</td>
</tr>
</tbody>
</table>
<h2 id="option-props"><a class="header-anchor" href="#option-props" aria-hidden="true">¶</a> Option Props</h2>
<table>
<thead>
<tr>
<th>参数</th>
<th>介绍</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>label</td>
<td>选项的标签</td>
<td>String</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>value</td>
<td>选项的值</td>
<td>—</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>ripple</td>
<td>点击是否有波纹效果</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>avatar</td>
<td>选项内容中是否有头像，会根据此参数调整列表项高度</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>disabled</td>
<td>选项是否被禁用</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
</tbody>
</table>

</div class="markdown-body"></template>

