      ],
      languages: ['Java', 'JavaScript', 'C#', 'C++', 'HTML', 'React', 'Vue', 'Muse-UI'],
      normal: {
        value1: '',
        value2: '',
        value3: '',
        value4: 'Option 1',
        value5: 'Option 2'
      },
      multi: {
        value1: [],
